<template>
	
	<view>
		<view class="store" @tap="icons()">
			<uni-icons :type="isicon?'star-filled':'star'" size="60"></uni-icons>
		</view>
		<view class="banner">
			
			<swiper class="swiper" indicator-dots=true>
				<swiper-item v-for="item in bannerList" :key="item.id">
					<view class="swiper-item uni-bg-red">
						<image :src="item.pic"></image>
					</view>
				</swiper-item>
				
			</swiper>
		</view>
		
		<view class="top">
			<view class="price">
				<view class="left">
					{{goodsinfo.promotionPrice}}
				</view>
				<view class="str">
					.00
				</view>
				<view class="center">
					￥{{goodsinfo.price}}
				</view>
				<view class="right">
					<view class="no">
						
					没有促销使用原价
					</view>
				</view>
				
			</view>
			
			<view class="name">
				{{goodsinfo.name}}
			</view>
			<view class="text">
				{{goodsinfo.description}}
			</view>
		</view>
		
		<view class="bottom" >
			<mp-html :tag-style="style"  :content=" goodsinfo.detailMobileHtml">
				
			</mp-html>
		
		</view>
	</view>
</template>

<script>
	import {productDetail} from'../../../api/classify/classifyList/index.js'
	export default{
		
		data(){
			return{
				goodsinfo:'',
				bannerList:'',
				style:{
					img:"width:100%; height:auto; margin:0 auto;",
					div:"width:100%; font-size:30rpx"
				},
				isicon:true,
				info:{},
				
			}
		},
		
		onLoad(options){
			productDetail(options.id).then(res=>{
			console.log(res)
			this.goodsinfo=res.data.product
			this.bannerList=this.goodsinfo.skuList
			
			var goods=uni.getStorageSync("goods");
			if(goods.length==0){
				this.isicon=false
			}else{
				var item =goods.find(ele=>ele.id==res.data.product.id);
					if(item){
						this.isicon=true
					}else{
						this.isicon=false
					}
			}
			
			})
			
			
	
		},
		
		methods:{
			icons(){
				var goods=uni.getStorageSync("goods");
				
				if(goods==undefined||goods==""){
					goods=[];
				}
				if(this.isicon==true){
					var item=goods.find(ele=>ele.id==this.goodsinfo.id);
					var index=goods.find(ele=>ele.id==this.goodsinfo.id)
					if(item){
						goods.splice(index,1)
					}else{
						goods.push(this.goodsinfo,index)
						
					}
				}else{
					goods.push(this.goodsinfo)
				}
				
				this.isicon=!this.isicon;
				uni.setStorageSync("goods",goods)
			}
			
		}
		
		
		
	}
	
</script>

<style lang="scss" scoped>
	
	@import  "./index.scss"
	
</style>
